html {
  background: #f1f1f1;
}

body {
  padding: 0;
  font-family: Azeret Mono, monospace;
}

@keyframes tailSwish {
  0% {
    rotate: none;
  }

  25% {
    rotate: -2deg;
  }

  27% {
    rotate: -28deg;
  }

  30% {
    rotate: -25deg;
  }

  36% {
    rotate: -25deg;
  }

  50% {
    rotate: -5deg;
  }

  75% {
    rotate: 10deg;
  }

  78% {
    rotate: 5deg;
  }

  98% {
    rotate: 5deg;
  }

  100% {
    rotate: none;
  }
}

@keyframes headLook {
  0% {
    rotate: none;
  }

  60% {
    rotate: none;
  }

  80% {
    rotate: -12deg;
  }

  100% {
    rotate: none;
  }
}

@keyframes bubbleFood {
  0% {
    rotate: none;
  }

  33% {
    margin-top: 1rem;
    rotate: 2deg;
  }

  50% {
    margin-top: 0;
  }

  67% {
    margin-top: .6rem;
    rotate: -2deg;
  }

  100% {
    rotate: none;
  }
}

@keyframes thots {
  0% {
    opacity: 0;
    top: 13rem;
    left: 13rem;
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    top: 12rem;
    left: 10rem;
  }
}

h3 {
  color: #56eeb8;
}

html, body, .content, .columns.content, article.column, #whiskerStage {
  height: 100%;
  min-height: 100%;
}

.container-full, .fullscreen {
  width: 100%;
  border-radius: 0;
  margin: 0 !important;
}

.container-full .column, .fullscreen .column {
  padding-top: 0;
  padding-bottom: 0;
}

#whiskerStage {
  background-color: #1f5080;
  background-size: cover;
  margin: 3rem 0;
  padding: 1.5rem;
  position: relative;
  overflow: hidden;
}

#whiskerStage.fullscreen {
  position: fixed;
  inset: 0;
}

#whiskerStage .button {
  z-index: 1100;
  position: relative;
}

#whiskerStage:before, #whiskerStage:after, #whiskerStage .modal.monitor, #whiskerStage .modal.monitor:before, #whiskerStage .modal.monitor:after {
  content: " ";
  z-index: 1;
  position: fixed;
}

#whiskerStage:after {
  opacity: 1;
  width: 100%;
  min-height: 39%;
  z-index: 1700;
  background: url("background-desk.b6f473e1.svg") 0 0 / cover no-repeat;
  display: block;
  bottom: 0;
  left: 0;
}

#whiskerStage nav, #whiskerStage aside, #whiskerStage progress, #whiskerStage span.tag {
  z-index: 100;
  position: absolute;
}

#whiskerStage nav {
  z-index: 1900;
  transition: all .3s ease-in-out;
  position: absolute;
  bottom: 1.2vw;
  left: 0;
  right: 0;
}

#whiskerStage nav .button {
  width: 17%;
  min-height: 5rem;
  color: #1a2a60;
  background: #f6ca83;
  border-radius: .1rem;
  margin: .5%;
  padding: 2rem 0;
  transition: background .1s linear;
  position: relative;
  box-shadow: 0 9px #f2b148;
}

#whiskerStage nav .button:hover {
  border-radius: .25rem;
  top: 1px;
  box-shadow: 0 8px #f0a630;
}

#whiskerStage nav .button:active {
  box-shadow: none;
  background: #f4c06b;
  top: 8px;
}

#whiskerStage nav .button:active.correct {
  background: #629460 !important;
}

#whiskerStage nav .button:first-of-type {
  margin-left: 2%;
}

#whiskerStage nav .button:last-of-type {
  margin-right: 2%;
}

#whiskerStage nav .button em {
  margin-top: .5vw;
  padding-right: 1.25rem;
  display: inline-block;
  position: relative;
}

#whiskerStage nav .button em img {
  max-width: 4vw;
  max-height: 3.333vw;
}

#whiskerStage.waiting progress, #whiskerStage.waiting .tag.game-time {
  top: -12vw;
}

#whiskerStage.waiting nav {
  bottom: -12vw;
}

#whiskerStage.waiting .cat-bubble {
  width: 1rem;
  height: 1rem;
  top: 0;
  left: 0;
}

#whiskerStage progress {
  width: 72%;
  height: 3rem;
  border: 1rem solid #1a2a60;
  border-radius: 0;
  transition: all .3s linear;
  position: absolute;
  top: 0;
  left: 0%;
}

#whiskerStage .tag.game-time {
  width: 8.5%;
  background-color: #15214c;
  border-radius: 0;
  font-size: 1.5rem;
  transition: all .3s linear;
  position: absolute;
  top: 0;
  right: 19.5%;
}

#whiskerStage .tag.game-time img {
  width: 1.25rem;
  height: auto;
  opacity: .5;
  fill: #fff;
  margin: .67rem 1rem 0 -.825rem;
}

#whiskerStage .modal {
  min-height: 344px;
  background-color: #031835;
  border: .7rem solid #3998b7;
  justify-content: flex-start;
  position: absolute;
  inset: 7.5rem 33% 30% 4.5%;
  box-shadow: -.5rem .5rem 0 0 #296b7b;
  z-index: 1330 !important;
  overflow: visible !important;
}

#whiskerStage .modal.monitor-placeholder {
  display: block !important;
}

#whiskerStage .modal:before {
  min-width: 55%;
  background: url("foreground-light.e3332d2b.svg") 0 100% / contain no-repeat;
  display: block;
  top: 6%;
  bottom: 18%;
  left: -3%;
  z-index: 1650 !important;
}

#whiskerStage .modal:after {
  height: 18%;
  width: 466px;
  background: url("background-monitorbase.e1b2f247.svg") bottom / contain no-repeat;
  margin-left: -15%;
  display: block;
  left: 35%;
  z-index: 1300 !important;
  position: fixed !important;
  bottom: 11% !important;
}

#whiskerStage .modal .modal-content {
  height: 25rem;
  width: 100%;
  z-index: 2020;
  background-color: #0000;
  align-self: flex-start;
  margin: 0 auto;
  padding-left: 13.333%;
  display: flex;
  position: relative;
  max-height: auto !important;
}

@keyframes ivanCursor {
  0% {
    opacity: 0;
  }

  50% {
    opacity: .5;
  }

  100% {
    opacity: 0;
  }
}

#whiskerStage .modal .modal-content .ivan {
  color: #56eeb8;
  padding-top: 3.5rem;
  transition: all .3s ease-in-out;
  animation: ivanCursor 3s infinite;
}

#whiskerStage .modal .box {
  z-index: 2150;
  width: 100%;
  min-height: 25rem;
  color: #fff;
  background-color: #0000;
  align-items: center;
  padding: 3rem;
}

#whiskerStage .modal .box h1 {
  color: #56eeb8;
  text-shadow: -.35vw .35vw #431439;
  font-size: 3.5rem;
  font-weight: bold;
  line-height: 1;
}

#whiskerStage .modal .box strong {
  color: #f6ca83;
}

#whiskerStage .modal .box .button {
  background: #3ad0c6;
  margin-top: 1rem;
  padding: 1.5rem 3rem;
  font-family: Azeret Mono, monospace;
  font-size: 1.5rem;
  font-weight: bold;
}

#whiskerStage #gameFinishModal .modal-content {
  height: 27rem !important;
}

#whiskerStage #gameFinishModal .modal-content h1 {
  font-size: 3.5rem;
}

#whiskerStage #gameFinishModal .modal-content .button#gameStart {
  margin-top: .75rem;
  font-family: Azeret Mono, monospace;
}

#whiskerStage #gameFinishModal .modal-content .leaderboard {
  width: 100%;
  height: 9rem;
  margin-left: 3rem;
  padding: 2rem 5% 2rem 13.333%;
  position: absolute;
  top: 18rem;
  left: 0;
}

#whiskerStage #gameFinishModal .modal-content .leaderboard p {
  color: #fff;
}

#whiskerStage #gameFinishModal .modal-content .leaderboard p a {
  color: #3998b7;
}

#whiskerStage #gameFinishModal .modal-content .button.is-info {
  color: #031835;
  background-color: #3998b7;
  margin: 0;
  padding: 1.2rem;
  font-size: .825rem;
}

#whiskerStage #gameFinishModal .modal-content p small {
  font-size: .75rem;
}

#whiskerStage aside.score {
  width: 20%;
  text-align: center;
  background: url("background-shelf.fad7edcb.svg") 0 0 / cover no-repeat;
  padding: 1rem 2rem 2rem;
  top: 0;
  bottom: 0;
  right: 0;
}

#whiskerStage aside.score h2 {
  color: #f6ca83;
  font-size: .75rem;
}

#whiskerStage aside.score strong {
  color: #f6ca83;
  margin: -.825rem .5rem 0;
  font-size: 3rem;
  display: block;
}

#whiskerStage .slats-the-cat {
  width: 20rem;
  height: 30rem;
  z-index: 1405;
  background: url("slats-torso.64e56eb6.svg") 0 0 / contain no-repeat;
  position: absolute;
  bottom: -4.5%;
  right: 25%;
}

#whiskerStage .slats-the-cat .slats-head {
  animation: headLook 12s linear infinite;
  position: absolute;
  top: -8rem;
  left: -2rem;
}

#whiskerStage .slats-the-cat .slats-tail {
  width: 25rem;
  min-height: 15rem;
  transform-origin: 100% 0;
  background: url("slats-tail.5082d04e.svg") 0 0 / contain no-repeat;
  animation: tailSwish 12s linear infinite;
  display: inline-block;
  position: relative;
  top: 15rem;
  right: 5rem;
}

.cat-bubble {
  width: 15rem;
  height: 12rem;
  z-index: 1850;
  z-index: 1111;
  text-align: center;
  background: #ffffffeb;
  border-radius: 22rem;
  padding: 2rem;
  transition: all .3s;
  animation: bubbleFood 5s linear infinite;
  display: none;
  position: absolute;
  top: -13rem;
  left: -15rem;
}

.cat-bubble:before {
  width: 3rem;
  height: 3rem;
  content: " ";
  z-index: 1850;
  opacity: .8;
  background: #ffffffeb;
  border-radius: 50%;
  transition: all .3s ease-in-out;
  animation: thots 7s ease-in-out infinite;
  display: block;
  position: absolute;
  top: 12rem;
  left: 10rem;
}

.cat-bubble:after {
  content: " ";
  width: 7rem;
  height: 7rem;
  z-index: 1900;
  background: 0 0 / cover;
  margin: 0 auto;
  display: block;
  position: relative;
}

.slats-head {
  width: 12rem;
  min-height: 25rem;
  z-index: 1300;
  background: url("slats-head.d381f18c.svg") 0 0 / contain no-repeat;
  display: inline-block;
  position: relative;
}

.slats-head img {
  width: 100%;
  display: none;
  position: absolute;
  top: 6.33rem;
  left: -1.25rem;
}

.slats-head.slats-resting img.is-resting {
  display: inline-block;
}

.slats-head.slats-blink img.is-blink, .slats-head.slats-eating2 img.is-eating2 {
  display: inline-block !important;
}

.slats-head.slats-eating img.is-eating, .slats-head.slats-huh img.is-huh, .slats-head.slats-grumpy img.is-grumpy {
  display: inline-block;
}

#elements {
  position: relative;
}

#elements svg, #elements img {
  max-width: 20vw;
  height: auto;
}

#elements svg h3, #elements svg h4, #elements img h3, #elements img h4 {
  color: #56eeb8;
  margin: 1rem 0;
  font-weight: bold;
}

#elements ul {
  margin: 2rem 0;
}

#elements li {
  padding-top: 3rem;
}

#elements li .slats-head {
  padding-top: 16rem;
}

#elements li code {
  color: #031835;
  background: #3998b7;
}

.beef > .cat-bubble:after {
  background: url("beef.1cce34e8.svg") 50% / contain no-repeat;
}

.chicken > .cat-bubble:after {
  background: url("chicken.a50d49f8.svg") 50% / contain no-repeat;
}

.fish > .cat-bubble:after {
  background: url("fish.479f026c.svg") 50% / contain no-repeat;
}

.veg > .cat-bubble:after {
  background: url("veggies.06f8d33e.svg") 50% / contain no-repeat;
}

