html {
    background: #F1F1F1;
}
body {
    font-family: 'Azeret Mono', monospace;
    padding: 0;
}

$green:  #56EEB8;
$black:  #031835;
$white:  #fff;
$mauve:  #B287A3;
$yellow: #F4D35E;
$gold:   #F6CA83;
$success:#629460;

$dark1:   $black;
$dark2: #1A2A60;
$dark3: #1F5080;

$light3: #368CA1;
$light2: #3998B7;
$light1: #3AD0C6;
$light0:   $green;


@keyframes tailSwish {
    0%   { rotate: 0deg; }
    25%  { rotate: -2deg; }
    27%  { rotate: -28deg; }
    30%  { rotate: -25deg; }
    36%  { rotate: -25deg; }
    50%  { rotate: -5deg; }
    75%  { rotate: 10deg; }
    78%  { rotate: 5deg; }
    98%  { rotate: 5deg; }
    100% { rotate: 0deg; }
}

@keyframes headLook {
    0%   { rotate: 0deg; }
    60%  { rotate: 0deg; }
    80%  { rotate: -12deg; }
    100% { rotate: 0deg; }
}
@keyframes bubbleFood {
    0%   {
        rotate: 0deg;
    }
    33%  {
        rotate: 2deg;
        margin-top: 1rem; 
    }
    50% {
        margin-top: 0;
    }
    67%  {
        rotate: -2deg;
        margin-top: 0.6rem;
    }
    100% {
        rotate: 0deg;
    }
}
@keyframes thots {
    0%   { 
        top: 13rem;
        left: 13rem;
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
    100% {
        top: 12rem;
        left: 10rem;
        opacity: 0;
     }
}

h3 {
    color: $green;
}

html,
body,
.content,
.columns.content,
article.column,
#whiskerStage {
    height: 100%; 
    min-height: 100%;
}

.container-full,
.fullscreen {
    width: 100%;
    border-radius: 0;
    margin: 0 !important;

    .column {
        padding-top: 0;
        padding-bottom: 0;
    }
}

#whiskerStage {
    padding: 1.5rem;
    position: relative;
    margin: 3rem 0 3rem;
    overflow: hidden;
    background-color: $dark3;
    background-size: cover;

    &.fullscreen {
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
    }

    .button {
        position: relative;
        z-index: 1100;
    }

    &:before,
    &:after,
    .modal.monitor,
    .modal.monitor:before,
    .modal.monitor:after {
        position: fixed;
        content: " ";
        z-index: 1;
    }

    &:after {
        background: url('../images/background-desk.svg') no-repeat 0 0;
        background-size: cover;
        display: block;
        opacity: 1;
        width: 100%;
        min-height: 39%;
        bottom: 0;
        left: 0;
        z-index: 1700;
    }

    nav,
    aside,
    progress,
    span.tag {
        position: absolute;
        z-index: 100;
    }

    nav {
        position: absolute;
        bottom: 1.2vw;
        left: 0;
        right: 0;
        z-index: 1900;
        transition: all 0.3s ease-in-out;

        .button {
            width: 17%;
            margin: 0.5%;
            padding: 2rem 0;
            min-height: 5rem;
            border-radius: 0.1rem;
            background: $gold;
            color: $dark2;
            position: relative;
            transition: background 0.1s linear;
            box-shadow: 0 9px darken($gold, 12.5%);

            &:hover {
                border-radius: 0.25rem;
                top: 1px;
                box-shadow: 0 8px darken($gold, 17.5%);
            }

            &:active {
                top: 8px;
                box-shadow: none;
                background: darken($gold, 5%);

                &.correct {
                    background: $success !important;
                }
            }

            &:first-of-type {
                margin-left: 2%;
            }
            &:last-of-type {
                margin-right: 2%;
            }

            em {
                position: relative;
                display: inline-block;
                padding-right: 1.25rem;
                margin-top: 0.5vw;

                img {
                    max-width: 4vw;
                    max-height: 3.333vw;
                }
            }
        }
    }

    &.waiting {
        progress,
        .tag.game-time {
            top: -12vw;

        }
        nav {
            bottom: -12vw;
        }

        .cat-bubble {
            width: 1rem;
            height: 1rem;
            top: 0;
            left: 0;
        }
    }

    progress {
        position: absolute;
        top: 0; 
        left: 0%;
        width: 72%;
        transition: all 0.3s ease;
        border: 1rem solid $dark2;
        height: 3rem;
        border-radius: 0;
        transition: all 0.3s linear;
    }

    .tag.game-time {
        position: absolute;
        top: 0; 
        right: 19.5%;
        font-size: 1.5rem;
        width: 8.5%;
        border-radius: 0;
        transition: all 0.3s linear;
        background-color: darken($dark2, 5%);

        img {
            width: 1.25rem;
            height: auto;
            margin: 0.67rem 1rem 0 -0.825rem;
            opacity: 0.5;
            fill: white;
        }
    }

    .modal {
        z-index: 1330 !important;
        position: absolute;
        left: 4.5%;
        top: 7.5rem;
        right: 33%;
        bottom: 30%;
        min-height: 344px;
        justify-content: flex-start;
        background-color: $dark1;
        border: 0.7rem solid $light2;
        overflow: visible !important;
        box-shadow: -0.5rem 0.5rem 0 0rem darken($light3, 10%);

        &.monitor-placeholder {
            display: block !important;
        }

        &:before {
            background: url('../images/foreground-light.svg') no-repeat left bottom;
            background-size: contain;
            display: block;
            bottom: 18%;
            min-width: 55%;
            left: -3%;
            top: 6%;
            z-index: 1650 !important;
        }

        &:after {
            background: url('../images/background-monitorbase.svg') no-repeat center bottom;
            background-size: contain;
            display: block;
            height: 18%;
            width: 466px;
            z-index: 1300 !important;
            left: 35%;
            margin-left: -15%;
            bottom: 11% !important;
            position: fixed !important;
        }


        .modal-content {
            margin: 0 auto;
            max-height: auto !important;
            height: 25rem;
            width: 100%;
            align-self: flex-start;
            display: flex;
            padding-left: 13.333%;
            background-color: transparent;
            position: relative;
            z-index: 2020;

            @keyframes ivanCursor {
                0%    { opacity: 0; }
                50%   { opacity: 0.5; }
                100%  { opacity: 0; }
            }

            .ivan {
                color: $green;
                padding-top: 3.5rem;
                animation: ivanCursor 3s infinite;
                transition: 0.3s all ease-in-out ;
            } 
        }

        .box {
            z-index: 2150;
            background-color: transparent;
            padding: 3rem;
            width: 100%;
            min-height: 25rem;
            color: white;
            -webkit-box-align: center;
            -ms-flex-align: center;
            align-items: center;

            h1 {
                font-size: 3.5rem;
                font-weight: bold;
                line-height: 1;
                color: $green;
                text-shadow: -0.35vw 0.35vw rgba(67, 20, 57, 1);
            }

            strong {
                color: $gold;
            }

            .button {
                background: $light1;
                font-weight: bold;
                font-size: 1.5rem;
                padding: 1.5rem 3rem;
                margin-top: 1rem;
                font-family: Azeret Mono, monospace;
            }
        }
    }

    #gameFinishModal {

        .modal-content {
            height: 27rem !important;

            h1 {
                font-size: 3.5rem;
            }

            .button#gameStart {
                font-family: Azeret Mono, monospace;
                margin-top: 0.75rem;
            }

            .leaderboard {
                // background-color: lighten($dark2, 17.5%);
                position: absolute;
                left: 0;
                top: 18rem;
                width: 100%;
                height: 9rem;
                padding: 2rem 5% 2rem 13.333%;
                margin-left: 3rem;
                // text-align: center;

                p {
                    color: $white;

                    a {
                        color: $light2;
                    }
                }
            }

            .button.is-info {
                font-size: 0.825rem;
                padding: 1.2rem;
                background-color: $light2;
                color: $dark1;
                margin: 0;
            }

            p small {
                font-size: 0.75rem;
            }
        }
    }

    aside.score {
        width: 20%;
        right: 0;
        top: 0;
        bottom: 0;
        padding: 1rem 2rem 2rem 2rem;
        background: url('../images/background-shelf.svg') no-repeat 0 0;
        background-size: cover;
        text-align: center;

        h2 {
            color: $gold;
            font-size: 0.75rem;
        }

        strong {
            color: $gold;
            font-size: 3rem;
            margin: -0.825rem 0.5rem 0;
            display: block;
        }
    }

    .slats-the-cat {
        position: absolute;
        bottom: -4.5%;
        right: 25%;
        width: 20rem;
        height: 30rem;
        background: url('../images/slats-torso.svg') no-repeat 0 0;
        background-size: contain;
        z-index: 1405;

        .slats-head {
            position: absolute;
            top: -8rem;
            left: -2rem;
            animation: headLook 12s linear infinite;
        }

        .slats-tail {
            position: absolute;
            top: 15rem;
            right: 5rem;
            background: url('../images/slats-tail.svg') no-repeat 0 0;
            background-size: contain;
            width: 25rem;
            min-height: 15rem;
            display: inline-block;
            position: relative;
            transform-origin: 100% 0vh;
            animation: tailSwish 12s linear infinite;
        }
    }
}

.cat-bubble {
    background: rgba(255,255,255,0.92);
    border-radius: 22rem;
    width: 15rem;
    height: 12rem;
    display: none;
    position: absolute;
    z-index: 1850;
    top: -13rem;
    left: -15rem;
    transition: all 0.3s ease;
    animation: bubbleFood 5s linear infinite;
    z-index: 1111;
    text-align: center;
    padding: 2rem;

    &:before {
        width: 3rem;
        height: 3rem;
        display: block;
        border-radius: 50%;
        content: " ";
        position: absolute;
        z-index: 1850;
        top: 12rem;
        left: 10rem;
        background: rgba(255,255,255,0.92);
        transition: all 0.3s ease-in-out;
        opacity: 0.8;
        animation: thots 7s ease-in-out infinite;
    }

    &:after {
        display: block;
        content: " ";
        width: 7rem;
        height: 7rem;
        z-index: 1900;
        margin: 0 auto;
        background: red;
        position: relative;
        background: transparent;
        background-size: cover;
    }
}

.slats-head {
    background: url('../images/slats-head.svg') no-repeat 0 0;
    background-size: contain;
    width: 12rem;
    min-height: 25rem;
    display: inline-block;
    position: relative;
    z-index: 1300;

    img {
        display: none;
        position: absolute;
        top: 6.33rem;
        left:-1.25rem;
        width: 100%;
    }

    &.slats-resting {
        img.is-resting {
            display: inline-block;
        }
    }

    &.slats-blink {
        img.is-blink {
            display: inline-block !important;
        }
    }

    &.slats-eating2 {
        img.is-eating2 {
            display: inline-block !important;
        }
    }

    &.slats-eating {
        img.is-eating {
            display: inline-block;
        }
    }

    &.slats-huh {
        img.is-huh {
            display: inline-block;
        }
    }

    &.slats-grumpy {
        img.is-grumpy {
            display: inline-block;
        }
    }
}

#elements {
    position: relative;
    svg, img {
        max-width: 20vw;
        height: auto;

        h3, h4 {
            margin: 1rem 0;
            font-weight: bold;
            color: $green;
        }
    }

    ul {
        margin: 2rem 0;
    }

    li {
        padding-top: 3rem;

        .slats-head {
            padding-top: 16rem;
        }

        code {
            background: $light2;
            color: $black;
        }
    }
}

// game is activated!
.beef > .cat-bubble:after {
    background: url('../images/beef.svg') no-repeat 50% 50%;
    background-size: contain;
}

.chicken > .cat-bubble:after {
    background: url('../images/chicken.svg') no-repeat 50% 50%;
    background-size: contain;
}

.fish > .cat-bubble:after {
    background: url('../images/fish.svg') no-repeat 50% 50%;
    background-size: contain;
}

.veg > .cat-bubble:after {
    background: url('../images/veggies.svg') no-repeat 50% 50%;
    background-size: contain;
}
